.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  min-height: 74px;
  border-bottom: 1px solid rgba(84,84,84,.7);
}

@media (min-width: 320px) and (max-width: 1023px){
  .header {
    padding-top: 28px;
    min-height: 56px;
  }
}