.profile {
  margin: 0 auto;
  width: 100%;
  max-width: 880px;
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
}

@media (min-width: 320px) and (max-width: 1023px){
  .profile {
    max-width: 282px;
    justify-content: space-around;
    flex-direction: column;
  }
}